<template>
  <div class='all-content'>
    <el-alert title="请使用chrome浏览器打开" center type="warning"></el-alert>
    <span class='content-title'>会议记录小助手</span>
    <div class='page-header'>
      <div>
        <span>识别语言类型</span>
        <el-select v-model="langValue" 
          placeholder="请选择识别语言类型" 
          @change='onLangChange'
          style="margin-left: 10px">
          <el-option
            v-for="item in langOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class='header-button'>
        <el-button type="primary" :disabled='isRecording' @click="onRecordStart">开始记录</el-button>
        <el-button type="success" :disabled='!isRecording' @click="onRecordStop">结束记录</el-button>
        <el-button type="warning" :disabled='!recordText'  @click='onCopyButtonClick'>复制文本</el-button>
      </div>
    </div>
    <el-input
      id='contentInput'
      class='content-input'
      type="textarea"
      placeholder="请输入内容"
      v-model="recordText"
      :rows='9'
    >
    </el-input>
    <el-dialog
      title="提示"
      :visible.sync="showPayModal"
      width="30%"
      :before-close="handleClose">
      <div style="display: flex; align-items: center; justify-content: center; flex-direction: column">
        <span>开发不易，给开发者一点打赏吧！（扫描下面二维码进入元豆支付）</span>
        <img style="width: 150px; height: 100px" src="./assets/payImage.jpg" />
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'App',
  data() {
    return {
      recordText: '',
      recognition: null,
      isRecording: false,
      langValue: 'zh-CN',
      langOptions: [
        {
          label: '中文',
          value: 'zh-CN'
        },
        {
          label: '英文',
          value: 'en-US'
        },
        {
          label: '日语',
          value: 'ja-JP'
        },
        {
          label: '韩语',
          value: 'ko-KR'
        },
      ],
      recordCount: 0,
      showPayModal: false
    }
  },
  computed: {
    isPCDevice() {
      return window.innerWidth > 600
    }
  },
  mounted(){
    document.title = '语音转文字识别平台'
  },
  methods: {
    onRecordStart() {
      if (this.recordCount === 5 && this.isPCDevice) {
        this.showPayModal = true
        this.recordCount++
        return
      } 
      this.recordCount++
      this.isRecording = true
      window.recognition = window.recognition || new window.webkitSpeechRecognition(); 
      this.recognition = window.recognition;
      this.recognition.lang = this.langValue;
      this.recognition.onend = () => {
        this.recognition.start(); // 在结束时重新启动识别
      };
      this.recognition.onresult = (e) => {
        const transcript = e.results[0][0].transcript;
        if (transcript) {
          this.recordText += transcript + ','
        }
      };
      this.recognition.start(); 
    },
    onRecordStop() {
      this.recognition.onend = () => {};
      this.isRecording = false
      this.recognition.stop();
    },
    onLangChange() {
      this.onRecordStop()
      this.recordText = ''
    },
    onCopyButtonClick() {
      navigator.clipboard.writeText(this.recordText)
        .then(() => {
          this.$message({
            message: '复制成功',
            type: 'success'
          });
        })
        .catch(() => {
          this.$message({
            message: '复制失败，请重新检查',
            type: 'error'
          });
        });
    },
    handleClose() {
      this.showPayModal = false
    } 

  },
}
</script>

